	import cpimg from "@/components/cpimg.vue"
	import { pathToBase64, base64ToPath } from '@/node_modules/image-tools/index.js' 
	export default {
		data() {
			return {
				src:'',
				data:Array,
				w:"",
				h:"",
				path:"",
				items: [{
                    value: 'one',
                    name: '一寸'
                },
                {
                    value: 'two',
                    name: '二寸',
                }
				],
			    items1: [{
                    value: 'red',
                    name: '红底'
                },
                {
                    value: 'blue',
                    name: '蓝底',
                },
                {
                    value: 'white',
                    name: '白底',
                }
				],
            current: 0,
			current1:0,
			inch:"one",
			bgcolor:"red"
			};
		},
		methods: {
			radioChange(evt) {
				//选择尺寸
			console.log(evt.detail.value)
			this.inch = evt.detail.value
        },
		radioChange1(evt) {
			//选择底色
			console.log(evt.detail.value)
			this.bgcolor = evt.detail.value
		},
			baocun(){
				uni.showLoading({
					title: '保存中'
				});
				base64ToPath(this.data.data)  
				  .then(path => {  
					console.log(path)
					  uni.saveImageToPhotosAlbum({
						filePath: path,
						success: function () {
							console.log('save success');
							uni.hideLoading();
						
						}
					});
				  })  
				  .catch(error => {  
					console.error(error)  
				  })  
			},
			cpimgWH(w,h){
				console.log("阿斯顿撒"+w);
				console.log("阿斯顿撒"+h);
				this.w= w
				this.h= h
			},
			cpimgPath(path){
				console.log('tu片'+path)
				this.path = path
			},
			// 添加图片
			addPhoto() {
				let that = this
				that.$refs.cpimg._changImg()
			},
			cpimgOk(file) {
				uni.showLoading({
					title: '加载中'
				});
				let that = this
				that.src = file
				//console.log(file)
				uni.request({
					url: 'https://www.qinendong.com/api/Face_recognition/Portrait_segmentation', //仅为示例，并非真实接口地址。
						data: {
							id: file.replace("data:image/png;base64,","").replace("data:image/jpeg;base64,",""),
							width:this.w,
							height:this.h,
							inch:this.inch,
							bgcolor:this.bgcolor
						},
						method:"POST",
					header: {
						'Content-type':'application/x-www-form-urlencoded' //自定义请求头信息
						},
						success: (res) => {
									//console.log(res.data.data);	
									 //this.data =  JSON.parse(res.data.data)
									console.log(res.data)
									this.data = res.data
									uni.hideLoading();
									//console.log(this.data.foreground) 
						}
					});
				
			},
			cpimgErr(e) {
				console.log(e)
			},
		},
		components: {
			cpimg
		},
		computed:{
			
		},
		
	}
